<script>
	import dayjs from 'dayjs';
	import datepickerStore from '../stores/datepicker';
	import { keyControlsContextKey, storeContextKey } from '../context';
	import { setContext } from 'svelte';
	import { derived } from 'svelte/store';
	import Popover from '../components/Popover.svelte';
	import Theme from '../components/generic/Theme.svelte';
	import Calendar from '../components/calendar/Calendar.svelte';
	import { fade } from 'svelte/transition';
	import { calendar as calendarDefaults } from '../config/defaults';

	export let selected = calendarDefaults.selected;
	export let start = calendarDefaults.start;
	export let end = calendarDefaults.end;
	export let format = calendarDefaults.format;
	export let formatted = '';
	export let theme = {};
	export let defaultTheme = undefined;
	export let startOfWeekIndex = 0;
	export let store = datepickerStore.get({
		selected,
		start,
		end,
		shouldEnlargeDay: true,
		startOfWeekIndex
	});

	setContext(storeContextKey, store);
	setContext(
		keyControlsContextKey,
		derived(store, ($s) => $s.activeView)
	);

	$: selected = $store.selected;
	$: formatted = dayjs(selected).format(format);
</script>

<Theme {defaultTheme} {theme} let:style>
	<Popover {style} let:key let:send let:receive bind:isOpen={$store.open}>
		<slot {key} {send} {receive} {formatted}>
			<div class="button-container">
				<svg class="icon" xmlns="http://www.w3.org/2000/svg" width="19" height="20" viewBox="0 0 19 20">
					<g transform="translate(0.5 0.5)">
						<rect width="18" height="17" rx="2.656" transform="translate(0 2)" fill="none" stroke="#748a9d" stroke-linecap="round" stroke-linejoin="round" stroke-width="1" />
						<line y2="4" transform="translate(4.5)" fill="none" stroke="#748a9d" stroke-linecap="round" stroke-linejoin="round" stroke-width="1" />
						<line y2="4" transform="translate(13.5)" fill="none" stroke="#748a9d" stroke-linecap="round" stroke-linejoin="round" stroke-width="1" />
						<circle cx="1.5" cy="1.5" r="1.5" transform="translate(3 4)" fill="none" stroke="#748a9d" stroke-linecap="round" stroke-linejoin="round" stroke-width="1" />
						<circle cx="1.5" cy="1.5" r="1.5" transform="translate(12 4)" fill="none" stroke="#748a9d" stroke-linecap="round" stroke-linejoin="round" stroke-width="1" />
						<line x2="12" transform="translate(3 11)" fill="none" stroke="#748a9d" stroke-linecap="round" stroke-linejoin="round" stroke-width="1" />
						<line x2="12" transform="translate(3 13)" fill="none" stroke="#748a9d" stroke-linecap="round" stroke-linejoin="round" stroke-width="1" />
						<line x2="12" transform="translate(3 15)" fill="none" stroke="#748a9d" stroke-linecap="round" stroke-linejoin="round" stroke-width="1" />
					</g>
				</svg>
				<button in:receive|local={{ key }} out:send|local={{ key }} />
				<span transition:fade|local={{ delay: 2 }} class="button-text">{formatted}</span>
			</div>
		</slot>
		<svelte:fragment slot="contents">
			<Calendar />
		</svelte:fragment>
	</Popover>
</Theme>

<style>
	.icon{
		z-index: 1;
		left:20px;
		position: absolute;
	}
	.button-container {
		display: grid;
	}
	.button-container > * {
		grid-column: 1;
		grid-row: 1;
		height: 100%;
	}
	button {
		padding: 16px 30px;
		background: transparent;
		color: var(--sc-theme-calendar-colors-text-primary);
		border: 0;
		cursor: pointer;
		background-color: rgba(78,93,106,1);
	}
	.button-text {
		padding: 16px 30px;
		color: rgba(166,188,208,1);
		cursor: pointer;
	}
</style>
